import React from 'react';
import css from './style.less';
import {Alert, Col, Container, FormControl, InputGroup, Row} from 'react-bootstrap';
import Header from '../component/Header';


const ruleMap = {
  'Led': ['Chaired']
};

const replaceAll = (str, search, replace) => str.split(search).join(replace);

class Home extends React.Component {
  state = {
    isConverting: false,
    originalResume: 'SW-TECH TRAINING SESSION   Seoul, Korea\n' +
    '   July 2017 – Oct 2017\n' +
    '•   Received first place in capstone project, using the dataset of income of data scientists.\n' +
    '•   Received training on statistics, R, and Tableau.\n' +
    '•   Networked with Korean government personnel.\n' +
    '\n' +
    '\n' +
    'Led a Project --> Chaired a Project',
    impactedResume: '',
  };

  handleResumeChange = async (value) => {
    this.setState(
      {originalResume: value, isConverting: true},
      () => {
        const original = (this.state.originalResume + ' ').trim();

        Object.keys(ruleMap)
          .map(key => ({key, value: ruleMap[key]}))
          .forEach(({key, value}) => replaceAll(original, key, value[0]));

        this.setState({impactedResume: original, isConverting: false});
      },
    );
  };

  renderInput = () => (
    <Col>
      <label className={css.label}>Paste your resume!</label><br />
      <FormControl
        className={css.resumeInput}
        as="textarea"
        rows="10"
        onChange={(e) => this.handleResumeChange(e.target.value)}
        value={this.state.originalResume}
      />
    </Col>
  );

  renderResult = () => (
    <Col>
      <label className={css.label}>Impacted!</label><br />
      {this.state.isConverting
        ? <p className={css.blueText}>Please wait...</p>
        : this.state.impactedResume
          ? (
            <Alert variant="success">
              {this.state.impactedResume.split('\n').map((e, i) => <p key={`${i}-${e.substring(0, 3)}`} className="mb-0">{e}</p>)}
            </Alert>
          ) : <div />
      }
    </Col>
  );

  render() {
    return (
      <div>
        <Header />
        <Container className="mt-5">
          <Row>
            {this.renderInput()}
            {this.renderResult()}
          </Row>
        </Container>
      </div>
    );
  }
}

export default Home;
